.button {
  background-color: rgb(89, 40, 6);
  border: solid 1px bisque;
  border-radius: 30px;
  font-size: 15px;
  padding: 4px 10px;
  margin: 50px 10px;
  font-size: 2rem;
}

.button:hover {
  /* background-color: bisque;
    border: solid 1px rgb(89, 40, 6); */
  cursor: pointer;
  box-shadow: 1px 1px 10px 10px bisque;
}

.link {
  color: bisque;
  text-decoration: none;
  text-shadow: 2px 2px 2px rgb(54, 24, 3);
}

.page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: saddlebrown;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-shadow: 2px 2px 2px rgb(54, 24, 3);
  width: 200vh;
  height: 100vh;
  background-repeat: no-repeat;
  background: url(../../countries.png) no-repeat center center fixed;
  background-size: 70% 70%;
}
